body {
	font: 14px "Century Gothic", Futura, sans-serif;
	margin: 20px;
}

.status {
	margin-bottom: 10px;
}

.square {
	background: #fff;
	border: 1px solid #999;
	float: left;
	font-size: 24px;
	font-weight: bold;
	line-height: 34px;
	height: 34px;
	margin-right: -1px;
	margin-top: -1px;
	padding: 0;
	text-align: center;
	width: 34px;
}

.kbd-navigation .square:focus {
	background: #ddd;
}

.leaderboard-title {
	flex-basis: content;
	text-align: center;
	padding-bottom: 2%;
	font-family: Georgia, serif;
	font-size: 25px;
	letter-spacing: 2.2px;
	word-spacing: -0.6px;
	color: #000000;
	font-weight: normal;
	text-decoration: rgb(68, 68, 68);
	font-style: normal;
	font-variant: small-caps;
	text-transform: capitalize;
}

.leaderboard {
	display: flex;
	flex-direction: column;
	flex: 0 1 auto;
	width: 15%;
	margin-right: auto;
}

.guess-tracker {
	flex: 0 1 auto;
	margin-left: auto;
	margin-right: auto;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
}

.player-box {
	flex-basis: auto;
	text-align: center;
	margin: 5px;
	background: #f2f2f2;
	border: 1px solid #ccc;
	box-shadow: 1px 1px 2px #fff inset, -1px -1px 2px #fff inset;
	border-radius: 3px/6px;

	font-family: Georgia, serif;
	font-size: 20px;
	letter-spacing: 2.2px;
	word-spacing: -0.6px;
	color: #000000;
	font-weight: normal;
	text-decoration: rgb(68, 68, 68);
	font-style: normal;
	font-variant: small-caps;
	text-transform: capitalize;
}

#place0 {
	background-color: #c9b037;
}

#place1 {
	background-color: silver;
}

#place2 {
	background-color: #ad8a56;
}

.shadow {
	-webkit-border-radius: 0% 0% 100% 100% / 0% 0% 8px 8px;
	-webkit-box-shadow: rgba(0, 0, 0, 0.3) 0 2px 3px;
}

.nav-bar {
	margin-left: auto;
	margin-right: auto;
}

.page {
	display: flex;
	flex-direction: row;
	align-items: stretch center;
	margin-top: 80px;
}

.player-input-container {
	margin-left: auto;
	margin-right: auto;
}

.player-input {
	display: flex;
	width: 60%;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-evenly;
	margin-left: auto;
	margin-right: auto;
}

.input-field {
	display: inline-block;
	margin: 5px;
}
.input-message {
	font-family: Georgia, serif;
	font-size: 16px;
	letter-spacing: 1px;
	word-spacing: 2px;
	color: #000000;
	text-align: center;
	font-weight: normal;
	text-decoration: none;
	font-style: normal;
	font-variant: normal;
	text-transform: none;
	padding-bottom: 2%;
}

#pad-right {
	padding-right: 3%;
	text-align: center;
	padding-top: 2%;
}

.round-count {
	text-align: center;
	padding-bottom: 2%;
	font-family: Georgia, serif;
	font-size: 32px;
	letter-spacing: 2.2px;
	word-spacing: -0.6px;
	color: #000000;
	font-weight: normal;
	text-decoration: rgb(68, 68, 68);
	font-style: normal;
	font-variant: small-caps;
	text-transform: capitalize;
}

.start-button {
	margin-top: 2%;
	text-align: center;
}

.player-guesses {
	width: 100%;
	margin-left: auto;
	margin-right: auto;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-content: space-between;
	justify-content: space-evenly;
	justify-content: center;
	align-items: center;
}

.guess-field {
	width: 12%;
	margin: 3px;
}

.new-game {
	background-color: lightseagreen;
}
